import { useCallback } from 'preact/hooks';
import { useEffect, useState } from "react";
import { dataEvents, getClosePlayers, getPlayer } from "./data";
import { debounce, throttle } from "./utils";

export function usePlayer() {
  const [player, setPlayer] = useState(getPlayer());
  useEffect(() => {
    function onPlayerUpdated() {
      const player = getPlayer();
      setPlayer(player);
    }
    window.addEventListener(dataEvents.playerUpdated, onPlayerUpdated);
    return () => {
      window.removeEventListener(dataEvents.playerUpdated, onPlayerUpdated);
    };
  }, []);
  return player;
}

export function usePlayerList() {
  const [playerList, setPlayerList] = useState([]);
  useEffect(() => {
    async function loadPlayers() {
      const players = await getClosePlayers();

      setPlayerList(players);
    }
    loadPlayers();
    window.addEventListener(dataEvents.playerUpdated, loadPlayers);
    window.addEventListener(dataEvents.playersLoaded, loadPlayers);
    return () => {
      window.removeEventListener(dataEvents.playerUpdated, loadPlayers);
      window.removeEventListener(dataEvents.playersLoaded, loadPlayers);
    };
  }, []);
  return playerList;
}
const noop = () => undefined;

const [onWindowEvent, offWindowEvent] = window.addEventListener
  ? [
      window.addEventListener.bind(window),
      window.removeEventListener.bind(window),
    ]
  : [noop, noop];

const isVisible = () => {
  const visibilityState = document.visibilityState;
  return visibilityState == null || visibilityState !== "hidden";
};

export const useFocus = (callback) => {
  const wrappedCallback = useCallback(throttle(callback, 5000), []);
  const onVisibleChange = () => {
    if (isVisible()) {
      wrappedCallback();
    }
  };
  document.addEventListener("visibilitychange", onVisibleChange);
  onWindowEvent("focus", wrappedCallback);
  return () => {
    document.removeEventListener("visibilitychange", onVisibleChange);
    offWindowEvent("focus", wrappedCallback);
  };
};
