import { css } from "goober";
import { useEffect, useRef, useState } from "react";
import { loadPlayers, setPlayerHighScore } from "../data";
import { useFocus, usePlayer } from "../hooks";
import { PlayerList } from './PlayerList';
import SimonSays from "./SimonSays";
import { UpdatePlayerModal } from './UpdatePlayerModal';
import {predefinedQuotes} from "../quotes";

const beforeStartQuotes = ['Click start to begin'];
export function App() {
  const styles = css`
    display: grid;
    place-items: center;
    .body-content {
        margin-top:4rem;
        display:flex;
        gap: 2rem;
        flex-direction:column;
        width:100%;
        align-items:center;

    }
    .top-bar {
      display: flex;
      align-items: center;
      padding: 0 1rem;
      letter-spacing: 1px;
      color: white;
      height: 3rem;
      position: fixed;
      top: 0;
      right:0;
      left:0;
      z-index: 1000;
      background-color: #323232;
      width: 100%;
      .high-score {
        font-weight: 500;
        font-size: 1.2rem;
        position: absolute;
        right: 1rem;
      }
      .player-name {
        font-weight: 500;
        font-size: 1.2rem;
        .icon {
          font-size: 0.8rem;
          position: relative;
          top: -0.4rem;
        }
        cursor: pointer;
        user-select: none;
      }
    }
  `;

  const [size, setSize] = useState(400);
  const player = usePlayer();
  function calculateSize() {
    const windowHeight = window.innerHeight - 80;
    let length =
      window.innerWidth < windowHeight ? window.innerWidth : windowHeight;
    let defaultWidth = length - 30;
    if (defaultWidth < 200) defaultWidth = 200;
    if (defaultWidth > 800) defaultWidth = 800;
    setSize(defaultWidth);
  }
  useEffect(() => {
    calculateSize();
    window.addEventListener("resize", calculateSize);
    return () => {
      window.removeEventListener("resize", calculateSize);
    };
  }, []);

  useFocus(loadPlayers)

  async function onGameOver(score) {
    if (score > player.highScore) {
      await setPlayerHighScore(score);
    }
  }
  const [gameState, setGameState] = useState();
  return (
    <div class={styles}>
      <UpdatePlayerModal />
      <div class="top-bar">
        <button
          type="button"
          class="player-name"
          data-bs-toggle="modal"
          data-bs-target="#change-player-name-modal"
        >
          {player?.name} <i class="bi bi-pencil-square icon" />
        </button>

        <span class="high-score">High Score: {player?.highScore}</span>
      </div>
      <div class="body-content">
        <SimonSays size={size} quotes={!gameState ? beforeStartQuotes: predefinedQuotes} updateGameState={setGameState} onGameOver={onGameOver} />
        <PlayerList />
      </div>
    </div>
  );
}


